@import url(https://fonts.googleapis.com/css2?family=Rock+Salt&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+2&family=Balsamiq+Sans&family=Caveat:wght@500&family=Red+Hat+Display:wght@700&family=Rock+Salt&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  font-family: 'Baloo 2', cursive;
  font-size: large;
}

.Background {
  background: #262626;
  position: fixed;
  width: 100%;
  z-index: -1;
  top: 0;
  bottom: -100%;
}

.WelcomeScreenWrapper {
  min-height: 100vh;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.WelcomeScreen {
  width: 50%;
  min-width: max(400px, 30vh);
  height: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: max(15px, 3vh) max(30px, 5%);
  gap: max(15px, 3vh) max(30px, 5%);
  margin: max(15px, 5vh);
}

.WelcomeScreen .WelcomePhoto {
  min-width: max(300px, 30vh);
  min-height: max(300px, 30vh);
  max-width: 30vh;
  max-height: 30vh;
  object-fit: cover;
  border-radius: 50%;
  -webkit-flex: 1 1;
          flex: 1 1;
  box-shadow: -10px 20px 20px #141414;
}

.WelcomeScreen .NoteCard {
  box-shadow: -10px 20px 20px #141414;
}

/*.WelcomeScreen .DownArrow {
  min-width: max(20px, 5vh);
  min-height: max(20px, 5vh);
  max-width: 5vh;
  max-height: 5vh;
  object-fit: contain;
  position: absolute;
  bottom: 0px;
  cursor: pointer;
}*/

.Section {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: max(30px, 8vh);
  gap: max(30px, 8vh);
  padding-bottom: max(30px, 15vh);
}

.Subsection {
  width: 50%;
  min-width: max(300px, 30vh);
  height: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: max(10px, 2vh) max(30px, 5%);
  gap: max(10px, 2vh) max(30px, 5%);
}

.EntryImage {
  min-width: max(200px, 20vh);
  min-height: max(200px, 20vh);
  max-width: 30vh;
  max-height: 30vh;
  object-fit: cover;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.SectionHeaderWrapper {
  width: 100%;
  height: max(40px, 5vh);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.SectionHeader {
  width: 80%;
  height: max(40px, 5vh);
  background: #1e1e1e;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: white;
  font-family: 'Rock Salt', cursive;
}

.NoteCardWrapper {
  min-width: max(300px, 30vh);
  -webkit-flex: 1 1;
          flex: 1 1;
}

.NoteCard {
  min-width: max(300px, 30vh);
  padding: max(15px, 2.5vh);
  -webkit-flex: 1 1;
          flex: 1 1;
  border-radius: 25px;
  background: #1e1e1e;
}

.NoteCardText {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: left;
          justify-content: left;
  grid-gap: max(15px, 2vh);
  gap: max(15px, 2vh);
}

.NoteCardHeader {
  color: #f0f0f0;
  font-family: 'Rock Salt', cursive;
}

.NoteCardDetails {
  color: #bfbfbf;
}

.NoteCard .Buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  grid-gap: max(15px, 5%) max(15px, 3%);
  gap: max(15px, 5%) max(15px, 3%);
}

.Button {
  text-align: center;
  color: #f0f0f0;
  border-radius: 10px;
  padding: 10px;
  border: 0px;
  background: #1e1e1e;
  text-decoration: underline;
  font-family: 'Rock Salt', cursive;
  font-size: 70%;
}

.Footer {
  width: 100%;
  height: max(60px, 10vh);
  background: #1e1e1e;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

@media screen and (-webkit-min-device-pixel-ratio: 1.0416666666666667), screen and (min-resolution: 100dpi) {
  .App {
    font-size: 200%;
  }
}

